.page {
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.imgContainer {
    margin-right: 50px;
    width: 50%;
}

.formDiv {
    margin-left: 100px;
}



.formWrapper {
    width: 60%;
    /* display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 10px; */
}

.error {
    color: red;
    margin-top: 5px;
    margin-bottom: 10px;
}


.formDiv h3 {
    margin-bottom: 30px;
}

.formDiv input {
    font-size: 16px;
    padding: 20px;
    width: 90%;
    border-radius: 5px;
    border: solid #7A7A7A 1px;
    margin-bottom: 10px;
}


[type="date"]::-webkit-inner-spin-button {
    display: none;
}

[type="date"]::-webkit-calendar-picker-indicator {
    display: none;
}

input[type=date]::-webkit-datetime-edit {
    color: #7A7A7A;
}

.formDiv input:focus {
    border: solid black 1px;
}

.formDiv input:valid {
    border: solid #84D0AB 1px;
}

.formDiv input:optional {
    border: solid #7A7A7A 1px;
}

.formDiv input:invalid {
    border: solid darkred 1px;
}

/* .formDiv .email {
    width: 100%;
} */

.formDiv button {
    margin-top: 15px;
    padding: 15px 30px;
}

.formDiv button:hover {
    color: white;
}

@media (max-width: 1150px) {
    .imgContainer {
        display: none;
    }
    .formWrapper {
        width: 80%;
    }

    /* .hero {
        padding: 15px 50px 15px 100px;
    } */

  }